<template>
    <div class="m-login">
      <!--<p>登录</p>-->
      <img src="" class="m-login-logo" alt="">
      <div  class="m-login-input m-login-bottom" >
        <input type="text" v-model="ustelphone" placeholder="请输入手机号码">
        <span class="m-code" @click="sendCode" v-if="!isSend">发送验证码</span>
        <span class="m-code cancel" v-else>{{count}}秒后重新发送</span>
      </div>
      <div type="text" class="m-login-input" >
        <input type="text" v-model="identifyingcode" placeholder="请输入验证码">
      </div>
      <!--<p class="m-login-forget" @click="forgetClick">忘记密码？</p>-->
      <div class="m-login-btn" @click="loginClick">登  录</div>

      <div class="m-wei-box">
        <p>
          <span class="m-icon-wei"></span>
        </p>
        <p>微信快速登录</p>
      </div>
    </div>


</template>

<script type="text/ecmascript-6">
  import axios from 'axios';
  import api from '../../api/api';
  import common from '../../common/js/common';
  import {Toast} from 'mint-ui';
    export default {
        data() {
            return {
                name: '',
              ustelphone:'',
              identifyingcode:'',
              isSend:false,
              timer:null,
              count:''
            }
        },
        components: {},
        methods: {
          sendCode(){
            if(this.ustelphone){
              const TIME_COUNT = 60;
              axios.get(api.get_inforcode,{
                params:{
                  ustelphone:this.ustelphone
                }
              }).then(res => {
                if(res.data.status ==200){

                }
              });
              if (!this.timer) {
                this.count = TIME_COUNT;
                this.isSend = true;
                this.timer = setInterval(() => {
                  if (this.count > 0 && this.count <= TIME_COUNT) {
                    this.count--;
                  } else {
                    this.isSend = false;
                    clearInterval(this.timer);
                    this.timer = null;
                  }
                }, 1000)
              }
            }else{
              Toast({ message: '请先输入手机',duration:1000, className: 'm-toast-fail' });
            }

          },
          loginClick(){
            if(!this.ustelphone){
              Toast({ message: '请先输入手机',duration:1000, className: 'm-toast-fail' });
              return false;
            }else if(!this.identifyingcode){
              Toast({ message: '请先输入验证码',duration:1000, className: 'm-toast-fail' });
              return false;
            }

            axios.post(api.login,{
              ustelphone:this.ustelphone,
              identifyingcode:this.identifyingcode
            }).then(res => {
              if(res.data.status == 200){
                localStorage.setItem('token',res.data.data.token);
                this.$router.push('/');
              }else{
                Toast({ message: res.data.message,duration:1000, className: 'm-toast-fail' });
              }
            },error =>{
              Toast({ message: error.data.message,duration:1000, className: 'm-toast-fail' });
            });

          }

        },
      mounted(){
        common.changeTitle('登录');

      },
        created() {

        }
    }
</script>
<style lang="less" rel="stylesheet/less" scoped>
.m-login{
  min-height: 100vh;
  background: url("/static/images/login-bc.png") no-repeat;
  background-size: 100% 100%;
  text-align: center;
  color: #fff;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  font-size: 38px;
  .m-login-logo{
    display: block;
    width: 280px;
    height: 280px;
    border-radius: 50%;
    background-color: #a4a4a4;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.16);
    margin: 71px 0 127px;
  }
  .m-login-input{
    width: 600px;
    height: 100px;
    border-radius: 10px;
    box-shadow: 0 5px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: rgba(255,255,255,0.2);
    color: #333;
    text-align: left;
    /*&.m-login-bottom{*/
      margin-bottom: 45px;
    /*}*/
    .m-code{
      display: inline-block;
      padding: 6px 18px;
      border-radius: 10px;
      background-color: #FCD316;
      font-size: 21px;
      color: #fff;
      box-shadow:0px 3px 6px rgba(0,0,0,0.16);
      &.cancel{
        background-color: #ccc;
      }
    }
    input{
      display: inline-block;
      width: 308px;
      padding-left: 52px;
      height: 100px;
      border-radius: 10px;
      border: none;
      text-align: left;
      font-size: 28px;
      background-color: transparent;
      color: #333;
    }
  }
  .m-login-forget{
    width: 600px;
    text-align: right;
    font-size: 24px;
    margin: 8px 0;
  }
  .m-login-btn{
    width: 600px;
    height: 100px;
    line-height: 100px;
    border-radius: 10px;
    box-shadow: 0 5px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: #fcd316;
  }
  .m-wei-box{
    margin-top: 158px;
    color: #fff;
    font-size: 18px;
    .m-icon-wei{
      display: inline-block;
      width: 65px;
      height: 65px;
      background: url("/static/images/icon-wei.png") no-repeat;
      background-size: 100% 100%;
    }
  }
}
input::-webkit-input-placeholder{
  color: #333;
}
input::-moz-placeholder{  //不知道为何火狐的placeholder的颜色是粉红色，怎么改都不行，希望有大牛路过帮忙指点
  color: #333;
}
input:-ms-input-placeholder{  //由于我的IE刚好是IE9，支持不了placeholder，所以也测试不了(⊙﹏⊙)，有IE10以上的娃可以帮我试试
  color: #333;
}
</style>



